@import url(https://fonts.googleapis.com/css?family=Anton);
@import url(https://fonts.googleapis.com/css?family=Montserrat:100,200,300,400,500,600,700,800,900);
/* App css */

.Image-section {
  background-image: url(/static/media/bglanding.9d28b0cc.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  height: 100vh;
  max-width: 100%;
}

.Jumbotron {
  padding-top: 2rem;
  height: 350px;
  background-image: url(/static/media/bgstories.26ac4a72.jpg);
  background-size: cover;
  overflow: hidden;
}
.list-ic li {
  position: relative;
}
.list-ic li span {
  display: inline-block;
  font-weight: 800;
  width: 1em;
  height: 1em;
  text-align: center;
  line-height: 3em;
  border-radius: 50%;
  background: #18bc9c;
  position: relative;
}
.list-ic li::before {
  content: '';
  position: absolute;
  background: rgba(192, 192, 192, 0.9);
}
.list-ic.vertical {
  padding: 0;
  margin: 0;
}
.list-ic.vertical li {
  list-style-type: none;
  text-align: left;
}
.list-ic.vertical li::before {
  top: -25px;
  left: 31px;
  width: 0.1em;
  height: 10em;
  visibility: visible;
}

.card {
  position: relative;
  background: white;
  margin-left: 30px;
  margin-left: 3rem;
  height: 6.5rem;
  width: 28rem;
  border-radius: none;
  border: 1px solid black;
}
.card:after,
.card:before {
  content: '';
  position: absolute;
  right: 100%;
  top: 50%;
  width: 0;
  height: 0;
}
.card:after {
  border-top: 20px solid transparent;
  border-right: 20px solid white;
  border-bottom: 20px solid transparent;
  border-left: 20px solid transparent;
  margin-top: -20px;
}
.card:before {
  border-top: 21px solid transparent;
  border-right: 21px solid black;
  border-bottom: 21px solid transparent;
  border-left: 21px solid transparent;
  margin-top: -21px;
}

.Email-input input {
  width: 100%;
  background-color: #fff;
  border: 1px solid #fff;
  text-align: center;
  height: 3rem;
  font-size: 15px;
}

.segment-value {
  font-family: 'Montserrat', 'sans-serif';
}

.speedo-segment:nth-of-type(1) {
  fill: rgb(95, 191, 130);
}

.speedo-segment:nth-of-type(2) {
  fill: rgb(52, 184, 111);
}

.speedo-segment:nth-of-type(3) {
  fill: rgb(41, 154, 92);
}

.speedo-segment:nth-of-type(4) {
  fill: rgb(206, 142, 78);
}

.speedo-segment:nth-of-type(5) {
  fill: rgb(207, 125, 78);
}

.speedo-segment:nth-of-type(6) {
  fill: rgb(212, 95, 75);
}

.speedo-segment:nth-of-type(7) {
  fill: rgb(206, 76, 52);
}

.speedo-segment:nth-of-type(8) {
  fill: rgb(183, 32, 37);
}

.segment-value:nth-of-type(1) {
  display: none;
}

html {
  min-height: 100%;
}

body {
  padding: 0;
  margin: 0;
  min-height: 100%;
  display: inline-block;
  background-color: white;
  /**overflow-x: hidden;**/
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  font-family: 'Montserrat', sans-serif;
}

